.social-btn {
    display: flex;
    width: 180px;
    align-items: center;
    justify-content: space-between;
    padding-left: 30px;
    padding-right: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    border: 1px;
    border-bottom-right-radius: 30px;
    border-top-right-radius: 30px;
    margin-bottom: 5px;
    position: relative;
    left: -170px;
    bottom: 90%;
    transition: left 1s;
  }
  .social-btn:hover {
    left: -10px;
    transition: left 1s;
  }
  
  .social {
    position: fixed;
    top: 50px;
  }
  .social a {
    text-decoration: none;
  }
  
  .color-telegram {
    background-color: #0084c6;
  }
  .color-instagram {
    background-color: #f62782;
  }
  .color-whatsapp {
    background-color: #24cc63;
  }
  
  .google-font {
    font-family: "Lato", sans-serif;
    font-size: 1.25rem;
  }
  
  .social-btn img {
    width: 40px;
  }
  
  .social-btn p {
    color: white;
  
    margin-top: 0px;
    margin-bottom: 0px;
  }
  
  .icons8-telegram-app {
    order: 2;
    display: inline-block;
    width: 40px;
    height: 40px;
    background: url("data:image/svg+xml;base64,")
      50% 50% no-repeat;
    background-size: 100%;
  }
  
  .icons8-instagram {
    order: 2;
    display: inline-block;
    width: 40px;
    height: 40px;
    background: url("data:image/svg+xml;base64,")
      50% 50% no-repeat;
    background-size: 100%;
  }
  
  .icons8-whatsapp {
    order: 2;
    display: inline-block;
    width: 38px;
    height: 38px;
    background: url("data:image/svg+xml;base64,")
      50% 50% no-repeat;
    background-size: 100%;
  }
  